
@media (min-width: 800px) {
    /* Nav Bar */
    .navbar {
        width: 95%;
    }
    .nav-menu {
        width: 95.2%;
    }
    .nav-menu.active {
        top: 12.1vh;
        left: 1.4vw;
    }
    .nav-links {
        font-size: 5vw;
    }
    /* Home Page */
    .presImg {
        height: 40vh;
    }
    .box {
        flex-direction: row !important;
        margin-top: 8vh;
    }
    .presentation {
        margin-right: 10vw;
    }
    /* Education Page */
    .education li {
        font-size: 2vw !important;
    }
    /* Contact Page */
    .icons {
        display: grid !important;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }
    .fa {
        width: 25vw !important;
    }
    .fa-whatsapp {grid-area: 1 / 1 / 2 / 2;}
    .fa-github {grid-area: 1 / 2 / 2 / 3;}
    .fa-linkedin {grid-area: 2 / 1 / 3 / 2;}
    .fa-envelope {grid-area: 2 / 2 / 3 / 3;}
    /* Experience Page */
    .experience li, .experience p {
        font-size: 2vw !important;
    }
}